@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=DotGothic16&display=swap');

body {
  background-color: black;
  color: #00ff00;
  margin: 0;
  padding: 0;
  font-family: 'DotGothic16', sans-serif; /* Impostato il font principale su DotGothic16 */
  /* Rimosso overflow: hidden; per consentire lo scorrimento */
}

/* Applica il font DotGothic16 anche a titoli e paragrafi */
h1, h2, h3, h4, h5, h6, p, a, button, span {
  font-family: 'DotGothic16', sans-serif;
}

.navbar-gradient, .footer-gradient {
  background: linear-gradient(0deg, #059669 85%, rgba(5, 150, 105, 0) 100%);
}

@keyframes pulse-custom {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(37, 99, 235, 0.7); /* Ombra blu */
  }
  70% {
    transform: scale(1.05);
    box-shadow: 0 0 0 10px rgba(37, 99, 235, 0);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(37, 99, 235, 0);
  }
}

.pulse-animation {
  animation: pulse-custom 2s infinite;
}

.pulse-animation:hover {
  animation: none;
}

@keyframes slideInFade {
  0% {
    opacity: 0;
    transform: translateX(50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.slide-in-section {
  animation: slideInFade 1.5s ease-out forwards;
}

.slide-in-section h1 {
  opacity: 0;
  animation: slideInFade 1.5s ease-out forwards;
  animation-delay: 0.5s;
}

.slide-in-section p {
  opacity: 0;
  animation: slideInFade 1.5s ease-out forwards;
  animation-delay: 1s;
}

@keyframes dealCard {
  0% {
    opacity: 0;
    transform: translateY(-100px) translateX(-50px) rotate(-20deg);
  }
  60% {
    transform: translateY(20px) translateX(10px) rotate(5deg);
  }
  80% {
    transform: translateY(-10px) translateX(-5px) rotate(-2deg);
  }
  100% {
    opacity: 1;
    transform: translateY(0) translateX(0) rotate(0);
  }
}

.card-trigger {
  opacity: 0;
}

.card-animate {
  animation: dealCard 1.5s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

.floating-element {
  animation: float 3s ease-in-out infinite;
}

@keyframes gradient-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.animated-circle {
  width: 200px;
  height: 200px;
  background: linear-gradient(45deg, #00ff7f, #4F4A4A);
  border-radius: 50%;
  animation: gradient-spin 8s linear infinite;
}

.animated-triangle {
  width: 0;
  height: 0;
  border-left: 100px solid transparent;
  border-right: 100px solid transparent;
  border-bottom: 173.2px solid #00ff7f;
  position: relative;
  animation: gradient-spin 8s linear infinite;
}

.animated-square {
  width: 200px;
  height: 200px;
  background: linear-gradient(45deg, #00ff7f, #4F4A4A);
  animation: gradient-spin 8s linear infinite;
}

.footer-gradient {
  background: linear-gradient(0deg, #059669 85%, rgba(5, 150, 105, 0) 100%);
}
.overflow-y-auto::-webkit-scrollbar {
  width: 8px;
}

.overflow-y-auto::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.overflow-y-auto::-webkit-scrollbar-thumb {
  background: rgba(0, 255, 127, 0.3);
  border-radius: 4px;
}

.overflow-y-auto::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 255, 127, 0.5);
}

.matrix {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* Rimosso overflow: hidden; per consentire lo scorrimento */
  z-index: -1;
}

.matrix span {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 20px;
  opacity: 0;
  animation: fall linear infinite;
}

@keyframes fall {
  0% {
    opacity: 1;
    transform: translateY(-100%);
  }
  100% {
    opacity: 0;
    transform: translateY(100%);
  }
}

@media (max-width: 768px) {
  .slide-in-section {
    animation-delay: 0.2s;
  }
  
  .slide-in-section h1 {
    animation-delay: 0.4s;
  }
  
  .slide-in-section p {
    animation-delay: 0.6s;
  }
  
  @keyframes dealCard {
    0% {
      opacity: 0;
      transform: translateY(-50px);
    }
    60% {
      transform: translateY(10px);
    }
    80% {
      transform: translateY(-5px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes float {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-10px); }
  }
}

/* Fix for very small screens */
@media (max-width: 480px) {
  .animated-circle, .animated-triangle, .animated-square {
    transform: scale(0.7);
  }
}
/* Contact Form Animations */
.animate-shrink {
  animation: shrink 3s linear forwards;
}

@keyframes shrink {
  0% {
    width: 100%;
  }
  100% {
    width: 0%;
  }
}

/* Form fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.bg-neutral-800 {
  animation: fadeIn 0.5s ease-out forwards;
}

/* Add styling for checkboxes */
.form-checkbox {
  appearance: none;
  -webkit-appearance: none;
  width: 1.25rem;
  height: 1.25rem;
  border: 2px solid #6ee7b7;
  border-radius: 4px;
  background-color: transparent;
  display: inline-block;
  position: relative;
  cursor: pointer;
}

.form-checkbox:checked {
  background-color: #6ee7b7;
}

.form-checkbox:checked::after {
  content: '✓';
  font-size: 0.875rem;
  color: #111827;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
}

/* Improve focus styles */
input:focus, select:focus, textarea:focus {
  box-shadow: 0 0 0 2px rgba(110, 231, 183, 0.3);
}
